@use '../abstracts'as *;

body {

    background-color: pink;
    margin: 10px;

}

.container {

    & h1 {

        color: green;

    }

}

.grid-container {
    display: grid;
    grid-template-columns: 50% 50%;
    background-color: #2196F3;
    padding: 10px;
}

.grid-item {
    background-color: rgba(255, 255, 255, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 30px;
    text-align: center;
}